.modal{position:relative;top:100px;margin:0 auto}.modal-wrap{position:fixed;overflow:auto;top:0;right:0;bottom:0;left:0;z-index:1050;-webkit-overflow-scrolling:touch;outline:0}.modal-mask{position:fixed;top:0;right:0;left:0;bottom:0;background-color:#373737;background-color:rgba(55,55,55,.6);height:100%;z-index:1050}.modal-mask-hidden{display:none}.modal-content{position:relative;background-color:#fff;border:none;border-radius:6px 6px;background-clip:padding-box}.modal-header{padding:13px 20px 14px 20px;border-radius:5px 5px 0 0;background:#fff;color:#666;border-bottom:1px solid #e9e9e9}.modal-body{padding:20px}.modal-footer{border-top:1px solid #e9e9e9;padding:10px 20px;text-align:right;border-radius:0 0 5px 5px}.modal-footer .button+.button{margin-left:8px}.modal-close{cursor:pointer;border:0;background:transparent;font-size:21px;position:absolute;right:20px;top:12px;font-weight:700;line-height:1;color:#000;text-shadow:0 1px 0 #fff;filter:alpha(opacity=20);opacity:.2;text-decoration:none}.modal-close-x:after{content:"×"}