// Utilities
@mixin font-base() {
  color: #000000d9;
  font-weight: 500;
  font-size: 30px;
  font-family: Avenir, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto,
    Helvetica Neue, Arial, Noto Sans, sans-serif, apple color emoji,
    segoe ui emoji, Segoe UI Symbol, noto color emoji, sans-serif;
}

.markdown {
  color: #000000d9;
  font-size: 14px;
  line-height: 2;
  padding-right: 170px;
  h1 {
    margin-top: 8px;
    margin-bottom: 20px;
    @include font-base();
  }
  h2,
  h3,
  h4,
  h5,
  h6 {
    clear: both;
    margin: 1.6em 0 0.6em;
    @include font-base();
  }
  h2 {
    font-size: 24px;
    line-height: 32px;
  }
  h3 {
    font-size: 18px;
  }
  // .input {
  //   padding: 0;
  //   border: none;
  //   outline: none;
  // }
  table {
    width: 100%;
    direction: ltr;
    border-collapse: collapse;
    empty-cells: show;
    border: 0;
  }
  > table th,
  > table td {
    text-align: left;
    border: 1px solid #f0f0f0;
  }
  .markdown.api-container table {
    font-size: 13px;
    font-family: SFMono-Regular, Consolas, Liberation Mono, Menlo, Courier,
      monospace;
    margin: 2rem 0;
    line-height: 1.5715;
  }
  .markdown.api-container table th,
  .markdown.api-container table td {
    padding: 12px;
    border-color: #f0f0f0;
    border-width: 1px 0;
    text-align: left;
  }
  .api-container table th:first-child,
  .api-container table td:first-child {
    border-left: 1px solid #f0f0f0;
  }
  &.api-container table td:first-child {
    width: 18%;
    color: #595959;
    font-weight: 600;
    white-space: nowrap;
  }
  &.api-container table td:nth-child(2) {
    width: 50%;
  }
  &.api-container table td:nth-child(3) {
    width: 22%;
  }
  &.api-container table td:nth-child(4) {
    width: 20%;
  }
  .exampleHidden {
    display: none;
  }
  .codeHidden {
    display: none;
  }
}
